.wp-block-yith-filled-block{
  padding-bottom: 60px;
  position: absolute!important;
  z-index: 1000;
  width: 100%;
  & .wp-block-yith-popover-filled-block {
    font-size: 12px;
    background: #fff;
    z-index: 1000;
    width: 100%;
    border-radius: 13px;
    box-sizing: border-box;
    border: 1px solid rgba(216, 216, 216, 1);
    box-shadow: 4px 2px 5px 1px rgba(0, 0, 0, 0.084181);
    padding: 23px;
    margin-top: 10px;
    max-height: 600px;
    overflow-y: auto;
    overflow-x: hidden;
    hr {
      border-bottom: 0;
    }
  }
  & .block-list-appender{
    width:100%;
    position: absolute;
    bottom: -100px;
  }
  & .wp-block-yith-related-posts-block{
    margin-bottom:0;
  }
}