
.wp-block-yith-empty-block{
  padding-bottom: 60px;
  position: absolute!important;
  z-index: 1000;
  width: 100%;
  & .wp-block-yith-popover-empty-block{
    font-size: 12px;
    background: #fff;
    z-index: 1000;
    width: 100%;
    border-radius: 13px;
    box-sizing: border-box;
    border: 1px solid rgba(216,216,216, 1);
    box-shadow: 4px 2px 5px 1px rgba(0, 0, 0, 0.084181);
    padding: 23px;
    margin-top:5px;
    position:relative;
  }
  & .block-list-appender{
    width:100%;
    position: absolute;
    bottom: -100px;
  }
}